import React from 'react'
import styled from 'styled-components'

import lines from './images/lines1.png'
import circle from './images/circle.png'

import html from './images/html5.svg'
import css3 from './images/css3.svg'
import sass from './images/sass.svg'
import php from './images/php.svg'
import js from './images/js.svg'
import vue from './images/vuejs.svg'
import react from './images/react.svg'
import figma from './images/figma.svg'
import illustrator from './images/illustrator.svg'
import wordpress from './images/wordpress.svg'


const Section = styled.section`
    position: relative;
    min-height: 450px;
    .lines {
        right: 0;
        top: 50%;
        transform: translateY(-40%);
        width: 500px;
        max-width: 75%;
    }
    .circle {
       bottom: -70px 
    }
    .list {
        list-style: none;
        padding: 0 0 0 16px;
        margin: 0;
        max-width: 600px;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
        grid-gap: 16px;
        li {
            display: grid;
            grid-template-columns: 20px 1fr;
            grid-gap: 10px;
            align-items: center;
            img {
                max-width: 100%;
            }
        }
    }
`

function Skills() {
    return (
        <Section>
            <img src={lines} alt="" className="background lines" />
            <img src={circle} alt="" className="background circle" />
            <div className="container">
                <h1>My skills</h1>
                <ul className="list">
                    <li>
                        <img src={html} alt="" />
                        <span>HTML</span>
                    </li>
                    <li>
                        <img src={css3} alt="" />
                        <span>CSS</span>
                    </li>
                    <li>
                        <img src={sass} alt="" />
                        <span>Sass</span>
                    </li>
                    <li>
                        <img src={js} alt="" />
                        <span>Javascript</span>
                    </li>
                    <li>
                        <img src={php} alt="" />
                        <span>PHP</span>
                    </li>
                    <li>
                        <img src={react} alt="" />
                        <span>React.js</span>
                    </li>
                    <li>
                        <img src={vue} alt="" />
                        <span>Vue.js</span>
                    </li>
                    <li>
                        <img src={figma} alt="" />
                        <span>Figma</span>
                    </li>
                    <li>
                        <img src={illustrator} alt="" />
                        <span>Illustrator</span>
                    </li>
                    <li>
                        <img src={wordpress} alt="" />
                        <span>WordPress</span>
                    </li>

                </ul>
            </div>
        </Section>
    )
}

export default Skills
