import React, { useState, Fragment } from 'react'

import Toast from './Toast'
import Input from './Input'

function Form() {

    const [toast, setToast] = useState(false)

    const handleSubmit = e => {
        e.preventDefault()
        setToast(true, setTimeout(() => setToast(false), 8000))

        const url = 'https://formspree.io/mdoklqbw'
        const conf = {
            body: new FormData(e.target),
            method: 'post'
        }
        fetch(url, conf)

        e.target.reset()
    }

    return (
        <Fragment>
            <form onSubmit={handleSubmit}>
                <Input label="Names" required={true} name="names" />
                <Input label="Email" required={true} type="email" name="email" />
                <Input label="Phone" type="number" name="phone" />
                <Input textarea label="How can I help you?" name="message" />
                <button className="button" type="submit">SEND</button>
            </form>
            {
                toast ? (
                    <Toast active={toast}>
                        Message sent succesfully
                    </Toast>
                ) : null
            }
        </Fragment>
    )
}

export default Form
