import styled from 'styled-components'

const Toast = styled.div`
    display: ${props => props.active ? 'block' : 'none'};
    text-align: center;
    position: fixed;
    background: #fff;
    color: #212121;
    box-shadow: 0 2px 16px -4px rgba(0, 0, 0, .25);
    border-radius: 10px;
    font-size: 16px;
    font-weight: 400;
    padding: 16px;
    width: fit-content;
    @media (min-width: 768px) {
        bottom: 10vh;
    }
    bottom: 10px;
    left: 10px;
    right: 10px;
    max-width: calc(100% - 20px);
    animation: fadeInUp 300ms cubic-bezier(0, 0.82, 0.48, 1.18);
    transform-origin: bottom;
    @keyframes fadeInUp {
        from {
            opacity: 0;
            transform: 
                translateY(100%);
        }
        to {
            opacity: 1;
            transform: 
                translateY(0%);
        }
    }
`
export default Toast