import React from 'react'
import styled from 'styled-components'

import lines from './images/lines.png'
import circle from './images/circle2.png'
import arrow from './images/arrow.png'


const Section = styled.section`
    align-items: center;
    h1 {
        max-width: 600px;
        margin-bottom: 1em;
    }
    .lines {
        top: 0;
        right: 0;
        max-width: 75%;
    }
    .circle {
        bottom: 20px;
        left: 10%;
        width: 160px;
        max-width: 30%;  
        @media (max-width: 600px) {
            bottom: 5%;
            left: 5%
        }  
    }
    .arrow {
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%);
        /* animation: float 1.5s infinite alternate;
        @keyframes float {
            from { transform: translateY(-5px) }
            to { transform: translateY(5px) }
        } */
    }
`

function Home() {
    return (
        <Section>
            <img className="background lines" src={lines} alt="" />
            <img className="background circle" src={circle} alt="" />
            <div className="container">
                <h1>Hi! Welcome to my web portfolio</h1>
            </div>
            <img src={arrow} alt="" className="background arrow" />
        </Section >
    )
}

export default Home
