import React from 'react'
import styled from 'styled-components'

const Input = (props) => {

    const InputField = styled('div')`
    display: ${props.hob ? 'none' : null};
    `
    const handleBlur = e => {
        let value = e.target.value,
            label = e.target.nextElementSibling

        value === '' ? label.className = '' : label.className = 'active'
    }

    return (
        <InputField className="input-field">
            {!props.textarea ?
                <input type={props.type} onBlur={handleBlur} required={props.required} name={props.name} />
                :
                <textarea onBlur={handleBlur} name={props.name} />}
            <label >{props.label}</label>
            <span></span>
        </InputField>
    )
}

export default Input