import React from 'react'
import styled from 'styled-components'

import entresillas from './images/entresillas.png'
import dekoclick from './images/dekoclick.png'
import grupoicono from './images/grupoicono.png'
import premiumsalud from './images/premiumsalud.png'
import bmwpremium from './images/bmwpremium.png'
import gadelgroup from './images/gadelgroup.png'

const Section = styled.section`
    align-items: flex-start;
    h1 {
        text-align: right;
    }
    .grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 1em;
        @media (max-width: 768px) {
            grid-template-columns: repeat(2, 1fr)
        }
        .card {
            background: rgba(255, 255, 255, 0.2);
            /* min-height: 350px; */
            position: relative;
            img {
                display: block;
            }
            .overlay {
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                padding: 1em;
                background: linear-gradient(rgba(250, 250, 250, 0), rgba(0, 0, 0, .5));
                display: flex;
                font-weight: 400;
                align-items: flex-end;
                opacity: 0;
                transition: 300ms;
            }
            &:hover {
                .overlay { opacity: 1}
            }
        }
    }
`

function Work() {
    return (
        <Section>
            <div className="container">
                <h1>My latest work</h1>
                <div className="grid">
                    <div className="card">
                        <img src={entresillas} alt="" />
                        <div className="overlay">
                            Entresillas
                        </div>
                    </div>
                    <div className="card">
                        <img src={gadelgroup} alt="" />
                        <div className="overlay">
                            Gadel Group
                        </div>
                    </div>
                    <div className="card">
                        <img src={grupoicono} alt="" />
                        <div className="overlay">
                            Grupo Icono
                        </div>
                    </div>
                    <div className="card">
                        <img src={dekoclick} alt="" />
                        <div className="overlay">
                            Dekoclick
                        </div>
                    </div>
                    <div className="card">
                        <img src={premiumsalud} alt="" />
                        <div className="overlay">
                            Premium Salud
                        </div>
                    </div>
                    <div className="card">
                        <img src={bmwpremium} alt="" />
                        <div className="overlay">
                            BMW Premium Selection
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}

export default Work