import React from 'react'
import styled from 'styled-components'

import Form from './Form'

import mail from './images/mail.svg'
import phone from './images/phone.svg'


const Section = styled.section`
    @media (min-width: 768px) {
        .grid {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 30px;
        }
    }
    h1 {
        text-align: right;
    }
    p {
        margin-bottom: 2em;
    }
    ul {
        list-style: none;
        padding: 0 0 0 16px;
        margin-bottom: 2em;
        li {
            margin-bottom: 16px;
            a {
                text-decoration: none;
                color: inherit;
                display: grid;
                grid-template-columns: 20px 1fr;
                grid-gap: 10px;
                align-items: center;
                img {
                    max-width: 100%;
                }
            }
        }
    }
`

function Contact() {
    return (
        <Section id="contact">
            <div className="container">
                <h1>Let's work together</h1>
                <div className="grid">
                    <article>
                        <p>If you're interest in hiring my service as a developer, just leave a message, and as soon as possible I will answer. <br /> You can also have my phone number and email.</p>
                        <ul>
                            <li>
                                <a href="mailto:rodrigueziriarte96@gmail.com">
                                    <img src={mail} alt="" />
                                    <span>rodrigueziriarte96@gmail.com</span>
                                </a>
                            </li>
                            <li>
                                <a href="tel:+51929543406">
                                    <img src={phone} alt="" />
                                    <span>+51929543406</span>
                                </a>
                            </li>
                        </ul>
                    </article>
                    <article>
                        <Form />
                    </article>
                </div>
            </div>
        </Section>
    )
}

export default Contact
